import React, { Component } from "react";

import { Alert } from "bootstrap";
export default class App extends Component {
  state = {
    seconds: 0,
    minutes: 0,
    hours: 0,
    intervals: [],
    disabled: false,
    buttonDown: false,
  };

  goMinutesDown = () => {
    const minutes = this.state;
    if (this.state.minutes != 0) {
      this.setState({
        minutes: this.state.minutes - 1,
      });
    }
  };
  goMinutesUp = () => {
    const minutes = this.state;
    if (this.state.minutes != 59) {
      this.setState({
        minutes: this.state.minutes + 1,
      });
    }
  };
  goHoursDown = () => {
    const hours = this.state;
    if (this.state.hours != 0) {
      this.setState({
        hours: this.state.hours - 1,
      });
    }
  };
  goHoursUp = () => {
    const hours = this.state;
    if (this.state.hours != 59) {
      this.setState({
        hours: this.state.hours + 1,
      });
    }
  };
  goSecondsUp = () => {
    const seconds = this.state;
    if (this.state.seconds != 59) {
      this.setState({
        seconds: this.state.seconds + 1,
      });
    }
  };

  goSecondsDown = () => {
    const seconds = this.state;
    if (this.state.seconds != 0) {
      this.setState({
        seconds: this.state.seconds - 1,
      });
    }
  };

  start = () => {
    let start_count = setInterval(() => {
      const { hours, minutes, seconds } = this.state;
      if (seconds === 0) {
        if (minutes === 0) {
          this.setState({
            minutes: 59,
            hours: hours - 1,
          });
          if (hours === 0) {
            this.setState({
              hours: 0,
              minutes: 0,
            });
          }
          if (hours === 0) {
            if (minutes === 0) {
              if (seconds === 0) {
                alert("TIME IS UP, 10 more seconds are given to reset timer");
                const { seconds } = this.state;
                this.setState({
                  seconds: this.state.seconds + 10,
                });
              }
            }
          }
        } else {
          this.setState({
            minutes: minutes - 1,
            seconds: 59,
          });
        }
      } else {
        this.setState({
          seconds: this.state.seconds - 1,
        });
      }
    }, 1000);
    this.setState({
      interval: start_count,
      disabled: true,
      buttonDown: true,
    });
  };
  stop = () => {
    clearInterval(this.state.interval);
    this.setState({
      disabled: false,
      buttonDown: false,
    });
  };
  reset = () => {
    clearInterval(this.state.interval);
    this.setState({
      hours: 0,
      minutes: 0,
      seconds: 0,
      disabled: false,
    });
  };
  Interval = () => {
    const { intervals, hours, minutes, seconds } = this.state;
    intervals.push(hours + ":" + minutes + ":" + seconds);
    this.setState({
      intervals,
    });
  };

  render() {
    const seconds = this.state.seconds;
    const hours = this.state.hours;
    const intervals = this.state.intervals;
    const minutes = this.state.minutes;
    const disabled = this.state.disabled;
    const buttonDown = this.state.buttonDown;
    return (
      <div className="container mt-5 border border-dark">
        <div className="row text-start offset-4">
          <div className="hours col-2">
            <button
              className="btn btn-success"
              disabled={disabled}
              onClick={this.goHoursUp}
            >
              +
            </button>
            <h1>{hours}:</h1>
            <button
              className="btn btn-danger"
              disabled={buttonDown}
              onClick={this.goHoursDown}
            >
              -
            </button>
          </div>
          <div className="minutes col-2">
            <button
              className="btn btn-success"
              disabled={disabled}
              onClick={this.goMinutesUp}
            >
              +
            </button>
            <h1>{minutes}:</h1>
            <button
              className="btn btn-danger"
              disabled={buttonDown}
              onClick={this.goMinutesDown}
            >
              -
            </button>
          </div>
          <div className="seconds col-2">
            <button
              className="btn btn-success"
              disabled={disabled}
              onClick={this.goSecondsUp}
            >
              +
            </button>
            <h1>{seconds}</h1>
            <button
              className="btn btn-danger"
              disabled={buttonDown}
              onClick={this.goSecondsDown}
            >
              -
            </button>
          </div>
        </div>
        <div className="row text-center offset-2 mt-4">
          <button
            className="btn col-2 btn-success"
            disabled={disabled}
            onClick={this.start}
          >
            Start
          </button>
          <button className="btn col-2 btn-danger" onClick={this.stop}>
            Stop
          </button>
          <button className="btn col-2 btn-info" onClick={this.reset}>
            Reset
          </button>
          <button className="btn col-2 btn-primary" onClick={this.Interval}>
            Interval
          </button>
        </div>
        {intervals.map((item, index) => {
          return (
            <div key={index}>
              <p>{item}</p>
            </div>
          );
        })}
        <App />
      </div>
    );
  }
}
